.form {
  padding-top: 40px;

  color: #ffffff;

  background-color: $color-title;
}

.form__text {
  margin: 0;
}

.form__block {
  padding-top: 15px;
  padding-bottom: 10px;

  text-align: center;

  background-color: $color-btn;

  border-radius: 4px;
}

.form__text {
  margin: 0;
  padding-left: 28px;
  padding-right: 28px;

  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
}

.form__discount {
  margin: 0;
  margin-left: 20px;

  font-size: 80px;
  font-weight: 700;
  letter-spacing: 1px;
}

.form__img {
  display: none;
}

.form__text-1 {
  margin: 0;
  padding-top: 15px;

  font-size: 20px;
  line-height: 35px;

  &--more-company {
    color: #ffffff;
  }
}

.form__box {
  margin-top: 30px;
  padding-bottom: 49px;
}

.form__box-wrap,
.form__box-label,
.form__box-input {
  width: 100%;
}

.form__box-label {
  font-size: 20px;
  font-weight: 700;

  &--more-company {
    color: #ffffff;
  }
}

.form__box-check-l,
.form__box-text {
  margin: 0;

  font-size: 14px;
  font-weight: 400;
  color: #C4C4C4;
  line-height: 0;
}

.form__box-text {
  margin-top: 10px;
}

.form__box-input {
  margin-top: 5px;
  margin-bottom: 17px;
  padding-top: 13px;
  padding-bottom: 11px;
  padding-left: 19px;

  font-size: $font-fira-sans;
  font-size: 18px;
  font-size: 18px;
  color: #444444;

  border: 1px solid #C4C4C4;
  border-radius: 6px;

  outline: none;
  border: none;

  box-shadow: 0 0 0 1px #c4c4c4;


  &:hover {
    box-shadow: 0 0 0 2px $color-btn;
  }

  &:focus,
  &:active {
    font-weight: 400;

    box-shadow: 0 0 0 1px $color-btn;
  }
}

.form__box-input::placeholder {
  font-size: $font-fira-sans;
  font-size: 18px;
  font-weight: 100;
  line-height: 28px;
  color: #979797;
}

.form__box-input:focus::placeholder {
  font-size: 0;
}

input::-moz-placeholder {
  font-size: 18px;
  line-height: 28px;
  color: #979797;
}

input:focus::-moz-placeholder {
  font-size: 0;
}

.form__box-wrap-btn {
  position: relative;
}

.form__box-check-in {
  display: none;
}

.form__box-check-l {
  display: block;

  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 12px;

  &--more-company {
    padding-bottom: 9px;
  }
}

.form__box-check-in + .form__box-check-l::before {
  content: "";

  position: absolute;
  top: 3px;
  left: 0;

  width: 12px;
  height: 12px;

  background-color: rgba(255, 255, 255, 0);
  border: solid 1px #DFE4E9;
  border-radius: 2px;

  cursor: pointer;
}

.form__box-check-in:checked + .form__box-check-l::before {
  content: "";

  background-image: url('../img/svg/v-mobile.svg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

@media #{$tablet-widths-v} {
  .form {
    padding-top: 80px;
    padding-bottom: 40px;
  }

  .form__wrap {
    width: 604px;
    margin: 0 auto;
  }

  .form__block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    padding-top: 60px;
    padding-bottom: 55px;
  }

  .form__text {
    width: 300px;
    padding: 0;
    padding-left: 30px;

    font-weight: 700;
    font-size: 29px;
    line-height: 44px;
    text-align: left;
  }

  .form__discount {
    font-size: 100px;
  }

  .form__text-1 {
    padding-top: 5px;

    &--more-company {
      text-align: center;
    }
  }

  .form__box {
    margin-top: 35px;

    &--more-company {
      width: 604px;
      margin: 0 auto;
      padding-top: 25px;
      padding-bottom: 90px;
    }
  }

  .form__box-input {
    margin-bottom: 41px;
    padding-top: 15px;
    padding-bottom: 13px;
  }
}

@media #{$tablet-widths-h} {
  .form {
    padding-top: 0;
    padding-bottom: 0;
  }

  .form__wrap {
    display: flex;
    justify-content: space-between;

    width: 1000px;
  }

  .form__box {
    &--more-company {
      width: 504px;
      margin-top: 35px;
      padding-left: 20px;
    }
  }

  .form__block {
    width: 394px;
    padding-top: 70px;

    text-align: center;
  }

  .form__text {
    width: 400px;
    margin-left: 0;
    padding: 0;
    padding-left: 28px;
    padding-right: 28px;

    font-weight: 400;
    font-size: 24px;
    line-height: 42px;
    text-align: center;
  }

  .form__discount {
    width: 400px;
    margin: 0;
    padding: 0;
    margin-left: 30px;

    font-size: 120px;
    text-align: center;
    letter-spacing: 1px;
  }

  .form__img {
    display: block;

    width: 173px;
    height: 171px;
    margin: 0;
    padding: 0;
    margin-left: 110px;
    margin-top: 25px;
  }

  .form__wrapper {
    width: 580px;
    padding-top: 30px;
  }

  .form__box {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;

    padding-right: 20px;
    padding-top: 5px;
  }

  .form__box-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

    width: 268px;

    &--more-company {
      width: 220px;
    }
  }

  .form__box-label,
  .form__box-input {
    width: 268px;

    &--more-company {
      width: 220px;
    }
  }

  .form__box-label--more-company span {
    display: none;
  }

  .form__box-wrap--quantity .form__box-label,
  .form__box-wrap--quantity .form__box-input,
  .form__box-wrap--quantity {
    width: 100%;
  }

  .form__box-wrap-btn {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

    width: 268px;
    margin-top: 15px;

    &--more-company {
      width: 100%;
      margin-top: 0;
    }
  }

  .form__box-check-l {
    margin-top: -10px;

    letter-spacing: -0.05em;

    &--more-company {
      margin-top: 0px;
      margin-bottom: 0px;
    }
  }

  .form__box-check-in + .form__box-check-l::before {
    top: -8px;
  }

  .form__box-check-in--more-company + .form__box-check-l--more-company::before {
    top: 3px;
    left: 3px;
  }

  .form__box-text {
    letter-spacing: -0.05em;
  }

  .form__box,
  .form__block {
    padding-bottom: 60px;
  }
}

@media #{$desktop-widths} {
  .form__wrap {
    width: 1142px;
  }

  .form__block {
    width: 464px;
    padding-top: 90px;
  }

  .form__text {
    padding: 0;
    padding-left: 65px;
    right: 65px;
  }

  .form__discount {
    width: 461px;
    margin-top: 25px;
  }

  .form__img {
    margin-left: 150px;
    margin-top: 50px;
    margin-bottom: 58px;
  }

  .form__wrapper {
    width: 678px;
    margin-top: 20px;
    padding-left: 118px;
  }

  .form__box {
    padding-right: 0;

    &--more-company {
      width: 463px;
      padding-left: 0;
      padding-bottom: 0;
    }
  }
}
