.btn {
  @include reset-btn();

  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 44px;
  padding-right: 44px;

  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;

  background-color: $color-btn;

  border: none;
  outline: none;
  border-radius: 6px;

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.16);

  cursor: pointer;

  &:hover {
    box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.25);
  }

  &:active {
    box-shadow: none;
  }

  &:disabled {
    box-shadow: none;

    border: dashed 1px #CBCBCB;

    opacity: 0.5;
  }
}
