.modal-confirmation__overlay {
  display: none;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  width: 100%;
  height: 100%;

  background: rgba(255, 255, 255, 0.9);
}

.modal-confirmation {
  display: none;

  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 11;

  width: 360px;
  margin-top: 20px;
  padding-top: 10px;
  padding-left: 28px;
  padding-right: 28px;

  background-color: $color-title;

  border-radius: 8px;
}

.modal-confirmation__close {
  position: absolute;
  top: 21px;
  right: 13px;
  z-index: 5;

  width: 38px;
  height: 38px;

  background-color: $color-title;

  border: none;
  outline: none;

  cursor: pointer;
}

.modal-confirmation__close {
  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 10px;
    right: 0;

    width: 38px;
    height: 2px;

    background-color: #ffffff;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.modal-confirmation__wrap {
  padding-top: 70px;
  padding-bottom: 130px;

  text-align: center;
}

.modal-confirmation__img {
  width: 141px;
  height: 141px;
}

.modal-confirmation__title {
  margin: 0;
  padding-top: 25px;
  padding-bottom: 10px;

  font-weight: 700;
  font-size: 24px;
  line-height: 37px;
  letter-spacing: -0.02em;
  color: #00FF57;

  &--error {
    color: #FF1414;
  }
}

.modal-confirmation__text {
  margin: 0;

  font-weight: 400;
  font-size: 20px;
  line-height: 35px;
  color: #ffffff;
}

@media #{$tablet-widths-v} {
  .modal-confirmation {
    position: relative;

    width: 600px;
    padding: 100px;
    padding: 20px 40px;

    background-color: $color-title;

    border-radius: 8px;
  }
}

@media #{$tablet-widths-h} {
  .modal-confirmation {
    position: relative;

    width: 758px;
    padding-top: 50px;
    padding-bottom: 72px;
    padding-left: 100px;
    padding-right: 100px;

    background-color: $color-title;

    border-radius: 8px;

    &--error {
      padding-bottom: 0;
    }
  }

  .modal-confirmation__img {
    width: 208px;
    height: 208px;

    margin-bottom: 20px;
  }

  .modal-confirmation__title {
    font-weight: 700;
    font-size: 40px;
    text-align: center;
    letter-spacing: -0.02em;
    line-height: 46px;
  }

  .modal-confirmation__text {
    padding-top: 10px;

    &--error {
      padding-top: 0;
    }
  }
}
