.title {
  margin: 0;

  font-size: 24px;
  font-weight: 700;
  color: $color-title;
  text-align: center;
  letter-spacing: -0.05em;
}

.title__industries {
  text-align: left;
}

.title__form {
  margin-top: 40px;

  color: #ffffff;
  text-align: left;
  letter-spacing: -0.02em;

  &--more-company {
    margin: 0;
    padding-top: 40px;
  }
}

.title__company {
  text-align: left;
}

.title__new-life {
  text-align: left;
  line-height: 37px;
  letter-spacing: -0.02em;
}

.title__reviews {
  margin: 0;

  font-weight: 700;
  font-size: 40px;
  line-height: 70px;
  text-align: center;
  letter-spacing: -1px;
}

.title__page-footer {
  margin: 0;

  font-weight: 500;
  font-size: 21px;
  line-height: 24px;
  letter-spacing: 4px;
  text-transform: uppercase;
  text-align: left;
}

@media #{$tablet-widths-v} {
  .title {
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    text-align: center;
    letter-spacing: -1px;
  }

  .title__form {
    text-align: left;
    letter-spacing: 0;

    &--more-company {
      padding-top: 70px;

      text-align: center;
    }
  }

  .title__company {
    text-align: left;
  }

  .title__page-footer {
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 4px;
    text-align: left;
  }
}

@media #{$tablet-widths-h} {
  .title__company {
    text-align: left;
  }

  .title__more-company {
    text-align: left;
  }
}
