.modal-form__overlay {
  display: none;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  width: 100%;
  height: 100%;

  background: rgba(255, 255, 255, 0.9);
}

.modal-form {
  display: none;

  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 11;

  width: 360px;
  max-height: 100%;
  padding-top: 10px;
  padding-left: 28px;
  padding-right: 28px;

  background-color: $color-title;

  border-radius: 8px;

  overflow: auto;
}


.modal-form__text-1 {
  color: #ffffff;
  font-size: 20px;
  line-height: 28px;
}

.modal-form__box {
  margin-top: 35px;
}

.modal-form__box-label {
  color: #ffffff;
}

.modal-form__close {
  position: absolute;
  top: 13px;
  right: 13px;
  z-index: 5;

  width: 38px;
  height: 38px;

  background-color: $color-title;

  border: none;
  outline: none;

  cursor: pointer;
}

.modal-form__close {
  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 10px;
    right: 0;

    width: 38px;
    height: 2px;

    background-color: #ffffff;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

@media #{$tablet-widths-v} {
  .modal-form {
    width: 600px;
    padding: 100px;
    padding: 20px 40px;

    background-color: $color-title;

    border-radius: 8px;
  }
}

@media #{$tablet-widths-h} {
  .modal-form {
    width: 758px;
    padding:0;
    padding-top: 55px;
    padding-bottom: 40px;
    padding-left: 100px;

    background-color: $color-title;

    border-radius: 8px;
  }

  .modal-form__wrapper {
    width: 568px;
    margin: 0;
    padding: 0;
  }

  .modal-form__box {
    padding-top: 12px;
    padding-right: 10px;
  }

  .modal-form__box-wrap {
    margin-bottom: 2px;
  }
}
