.page-header {
  position: relative;

  background-color: #EBEBEB;
}

.page-header .container {
  overflow: hidden;
  position: relative;
  z-index: 11;
}

.page-header__wrap {
  display: flex;
  flex-wrap: wrap;

  position: relative;

  padding-bottom: 14px;
}

@media #{$tablet-widths-v} {
  .page-header__wrap {
    justify-content: start;

    padding-top: 5px;
    padding-bottom: 20px;
  }

  .page-header .container {
    overflow: hidden;
    position: relative;
    z-index: 1;
  }
}
