.modal-nav__overlay {
  display: none;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  width: 100%;
  height: 100%;

  background: rgba(255, 255, 255, 0.9);
}

.main-nav {
  position: relative;
  z-index: 12;

  margin: 0 auto;
}

.main-nav__items {
  @include reset-list();

  display: block;

  position: relative;
  z-index: 12;

  width: 360px;
  margin-top: -45px;
  margin-left: -28px;
  padding-top: 100px;
  padding-bottom: 75px;
  padding-left: 28px;
  padding-right: 28px;

  background-color: #ffffff;

  &::before {
    content: '';

    position: absolute;
    top: 20px;
    left: 25px;

    width: 116px;
    height: 32px;

    background-image: url('../pic/svg/logo-menu.svg');
    background-position: center;
    background-repeat: no-repeat;
  }
}

.main-nav__item {
  margin-bottom: 24px;

  text-align: center;

  border: 1px solid #C4C4C4;
  border-radius: 6px;

  cursor: pointer;

  &--btn {
    margin: 0;

    border: none;
  }

  &:hover {
    border: 1px solid #EB391C;
  }

  &--btn:hover {
    border: none;
  }
}

.main-nav__link {
  display: inline-block;

  width: 100%;
  padding-top: 9px;
  padding-bottom: 9px;

  font-size: 20px;
  color: $color-title;
  text-decoration: none;

  &:hover,
  &:active {
    color: $color-btn;
  }
}

.main-nav--closed .main-nav__toggle {
  @include reset-btn();

  position: absolute;
  top: 20px;
  right: -120px;

  display: block;
  width: 18px;
  height: 21px;
  margin: 0;
  padding: 0;

  cursor: pointer;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";

  position: absolute;
  top: 0;
  right: 0;

  width: 18px;
  height: 2px;

  background-color: #444444;
  box-shadow: 0 7px 0 0 #444444, 0 15px 0 0 #444444;
}

.main-nav--opened .main-nav__toggle {
  position: absolute;
  top: -25px;
  right: 30px;
  z-index: 13;

  width: 30px;
  height: 30px;
  padding: 0;

  border: none;

  background-color: #ffffff;

  cursor: pointer;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 14px;
    right: 0;
    z-index: 5;

    width: 30px;
    height: 2px;

    background-color: #444444;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

@media #{$mobile-widths-menu} {
  .main-nav--closed .main-nav__items {
    display: none;
  }

  .main-nav--opened {
    display: block;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__items {
    display: block;
  }
}

@media #{$tablet-widths-v} {
  .main-nav {
    width: 276px;
    margin-top: 23px;
    margin-left: 20px;
    margin-right: 10px;
  }

  .main-nav__toggle::before {
    display: none;
  }

  .main-nav__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    position: static;

    width: 276px;
    margin: 0;
    padding: 0;

    background-color: #EBEBEB;

    transform: none;

    &::before {
      display: none;
     }
  }

  .main-nav__item {
    margin-bottom: 0;

    border: none;

    &:hover {
      border: none;
    }
  }

  .main-nav__item:nth-child(even) {
    display: none;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}

@media #{$tablet-widths-h} {
  .main-nav {
    width: 514px;
    margin-left: 25px;
    margin-right: 20px;
    margin-top: 25px;
  }

  .main-nav__items {
    width: 514px;
  }

  .main-nav__item:nth-child(even) {
    display: block;
  }

  .main-nav__link {
    font-size: 14px;
  }
}

@media #{$desktop-widths} {
  .main-nav {
    margin-left: 45px;
    margin-right: 50px;
  }
}
